import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { interval } from 'rxjs/internal/observable/interval';
import { UtilsService } from '../services/utils.service';

interface Menu {
  id: string,
  src: string,
  name: string,
  routerLink: string
}

@Component({
  selector: 'app-main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.css'],
})
export class MainMenuComponent implements OnInit {
  public showFiller = false;
  public main_menu: Menu[] = [];
  public showHead: boolean = false;
  private subscription: any;
  private secondsCounter$: any;

  constructor(private router: Router, private utilsService: UtilsService) {
    // on route change to '/login', set the variable showHead to false
    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        if (
          event['url'] == '/login' ||
          event['url'] == '/recovery' ||
          event['url'] == '/code-recovery' ||
          event['url'] == '/new-password'
        ) {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
  }

  ngOnInit(): void {
    const userRole: string | boolean = sessionStorage.getItem('Role') || false;

    if (userRole === 'ROLE_SUPERADMIN' || userRole === 'ROLE_ADMIN') {
      this.main_menu = [
        {
          id: 'users',
          routerLink: 'users', // 'users/admin-empresas', -> llega con ambos activos pero luego pierde el active del padre
          name: 'Administración de usuarios',
          src: '../../assets/images/UsersIcon.svg',
        },
        {
          id: 'evento',
          name: 'Eventos ',
          routerLink: 'events', // 'events/calendario-eventos', -> llega con ambos activos pero luego pierde el active del padre
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          name: 'Estadisticas',
          routerLink: 'reportes',
          src: '../../assets/images/ReporteIcon.svg'
        },
        {
          id: 'notificaciones',
          name: 'Notificaciones',
          routerLink: 'notificaciones',
          src: '../../assets/images/NotificacionIcon.svg'
        },
        {
          id: 'promociones',
          name: 'Beneficios',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    } else if (userRole === 'ROLE_ORGANIZADOR_EVENTOS') { //ROLE_ORGANIZADOR
      this.main_menu = [
        {
          id: 'evento',
          routerLink: 'events/calendario-eventos',
          name: 'Eventos',
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          routerLink: 'reportes',
          name: '',
          src: '../../assets/images/ReporteIcon.svg'
        }
      ]
    } else {
      // ROLE_ALIADO 
      this.main_menu = [
        {
          id: 'promociones',
          name: 'Beneficios',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    }

    this.validation();
  }

  // sidebar(){
  //   console.log('Sidebar')
  // }

  validation() {
    this.secondsCounter$ = interval(1200000); //15seg. = 15000
    this.subscription = this.secondsCounter$.subscribe(() => {
      const current = new Date().getTime();
      const getSession = localStorage.getItem('session')?.toString() || '';
      const expiration = new Date(parseInt(getSession.toString())).getTime();

      if(getSession === '' || (current >= expiration)){
        this.logout();
        this.utilsService.dialog('ERROR', 'Error', '¡Sesión expirada!' );
      }
    });
  }

  logout() {
    sessionStorage.removeItem('Token');
    sessionStorage.clear();
    localStorage.clear();

    this.subscription.unsubscribe();
    this.router.navigate([`/login`]);
  }
}
