import { Component, OnInit } from '@angular/core';
import cheerio from 'cheerio';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { EventService } from 'src/app/services/event.service';
import { UtilsService } from 'src/app/services/utils.service';
import { PromotionsService } from 'src/app/services/promotions.service';
import { NotificationItem } from 'src/app/models/notification.item.model';
import { NotificationsService } from 'src/app/services/notifications.service';

class CustomTimeClass {
  hours: string;
  minutes: string;
  seconds: string;

  constructor(hours: string, minutes: string, seconds: string) {
    this.hours = hours;
    this.minutes = minutes;
    this.seconds = seconds;
  }

  toString() {
    return `${this.hours}:${this.minutes}:${this.seconds}`;
  }
}

@Component({
  selector: 'app-edit-notificacion',
  templateUrl: './edit-notificacion.component.html',
  styleUrls: ['./edit-notificacion.component.css'],
})
export class EditNotificacionComponent implements OnInit {
  public loading: boolean = false;
  public selectedItems: any = [];
  public dropdownSettings: IDropdownSettings = {
    idField: 'id',
    textField: 'name',
    singleSelection: true,
    allowSearchFilter: true,
    closeDropDownOnSelection: true,
    searchPlaceholderText: 'Buscar'
  };

  public eventAndPromoList: any[] = [{ id: null, name: 'Seleccione...', type: '' }];

  public statusList = [
    { id: 'seleccione', name: 'Seleccione...', disabled: true },
    { id: 'scheduled', name: 'Programado', disabled: false },
    { id: 'instant', name: 'Instantáneo', disabled: false }
  ];

  public notification: NotificationItem = {} as NotificationItem;

  statusSelected: string = '';
  eventSelected: any = {};
  isDateAndHourSelectEnabled: boolean = false;

  subject: string = '';
  message: string = '';
  public dateStart!: NgbDateStruct;
  sendHour: CustomTimeClass | undefined;
  notificationModel: NotificationItem | undefined;
  notificationId: number | null = null;
  utcSendDate: string | undefined;
  utcSendHour: string | undefined;

  currentEditNotificationId: NotificationItem | undefined;

  constructor(
    private router: Router,
    private eventService: EventService,
    private utilsService: UtilsService,
    private Activatedroute: ActivatedRoute,
    private promotionService: PromotionsService,
    private notificationsService: NotificationsService,
  ) {
    this.loadActiveEvents();
    this.loadActivePromotions()
    this.notification.subject = '';
    this.notification.message = '';
    this.statusSelected = this.statusList[0].id;
    this.eventSelected = this.eventAndPromoList[0];
    this.notification.notificationType = 'seleccione';
  }

  ngOnInit() {
    this.Activatedroute.queryParams.subscribe((params) => {
      const notificationModelString = params['NotificationModel'];
      if (notificationModelString != undefined) {
        this.notificationModel = JSON.parse(notificationModelString);
      }
    });
  }

  async onItemSelect(item: any) {
    const selected = await this.eventAndPromoList.find(x => x.id === item.id);
    this.notification.eventId = selected.id;
    this.notification.eventType = selected.type;
    this.notification.eventId = Number(selected.id.substr(1));
  }

  onStatusChange($event: any) {
    this.dateStart = {} as NgbDateStruct;
    this.sendHour = {} as CustomTimeClass;
    this.isDateAndHourSelectEnabled = $event.target.value === 'instant';
    // this.isDateAndHourSelectEnabled = $event.target.value === 'Instantáneo';
  }

  setInitialNotificationData() {
    this.statusSelected = this.getfFrontStatus(
      this.notificationModel?.notificationType as string
    );
    this.eventSelected = this.getEventOrPromotionName();
    this.subject = this.notificationModel?.subject as string;
    this.message = this.notificationModel?.message as string;
    this.sendHour = this.getHourSend();
    this.notificationId = this.notificationModel?.id ?? null;
    this.dateStart = this.getInitialDate(
      this.notificationModel?.dateSend as string
    );
  }

  getEventOrPromotionName() {
    const element = this.eventAndPromoList.find(
      (e) => e.id === this.notificationModel?.eventId
    );
    return element;
  }

  getInitialDate(stringDate: string) {
    const dateParts = stringDate.split('-');
    return { year: +dateParts[0], month: +dateParts[1], day: +dateParts[2] };
  }

  getHourSend() {
    const hourAndMinutes = this.notificationModel?.hourSend!.split(':');
    const date = new Date();
    date.setHours(Number(hourAndMinutes![0]));
    date.setMinutes(Number(hourAndMinutes![1]));
    date.setSeconds(0);
    const hours = ('0' + date.getHours()).slice(-2);
    const minutes = ('0' + date.getMinutes()).slice(-2);
    const seconds = ('0' + date.getSeconds()).slice(-2);
    return new CustomTimeClass(hours, minutes, seconds);
  }

  onTimeInputChange(event: any) {
    const timeValue = event.target.value;
    const [hours, minutes] = timeValue.split(':').map((str: string) => {
      return parseInt(str, 10);
    });

    const formattedHours = hours.toString().padStart(2, '0');
    const formattedMinutes = minutes.toString().padStart(2, '0');

    this.sendHour = new CustomTimeClass(formattedHours, formattedMinutes, '00');
  }

  getfFrontStatus(status: string) {
    switch (status) {
      case 'scheduled':
        return 'Programado';
      case 'draft':
        return 'Programado';
      default:
        return 'Programado';
    }
  }

  async loadActivePromotions() {
    try {
      const response = await this.utilsService.getAllPromotions('active');
      // const response = await this.promotionService.getAllPromotions('active');
      if (response?.success === true) {
        // this.accountClosureData = [...accountClosure.transaction];
        this.eventAndPromoList = [...this.eventAndPromoList];
        response.result.forEach((e: any) => {
          this.eventAndPromoList.push({
            id: 'p' + e.id,
            name: e.name,
            type: 'promotion',
          });
        });
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
    }
  }

  loadActiveEvents() {
    this.eventService.getEvents().subscribe({
      next: async (res) => {
        // console.log(res.result)
        this.eventAndPromoList = [...this.eventAndPromoList];
        res.result.map((e: any) => {
          if (e.status === 'active') {
            this.eventAndPromoList.push({
              id: 'e' + e.id,
              name: e.title,
              type: 'event' // e.eventType.name // 'event',
            });
          };
        });

        if (this.notificationModel != undefined) {
          this.setInitialNotificationData();
        }

        // console.log(this.eventAndPromoList)
      },
      error: (e) =>
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        ),
    });
  }

  clearFields() {
    this.notification = {} as NotificationItem;
    this.notification.subject = '';
    this.notification.message = '';
    this.statusSelected = this.statusList[0].id;
    this.eventSelected = this.eventAndPromoList[0];
    this.notification.notificationType = 'seleccione';
  }

  async sendNotification() {
    this.loading = true;
    let response: any;

    if (this.notification.notificationType === 'instant') {
      this.notification.dateSend = this.utilsService.getDate('HH:mm');
      this.notification.hourSend = this.utilsService.getDate('yyyy-MM-dd');
      
      response = await this.notificationsService.createScheduleNotificacion(this.notification)
    }
    else if (this.notification.notificationType === 'scheduled') {
      this.notification.hourSend = `${this.sendHour!.hours}:${this.sendHour!.minutes}`;
      this.notification.hourSend = `${this.dateStart.year}-${this.dateStart.month}-${this.dateStart.day}`;

      response = await this.notificationsService.createInstantNotificacion(this.notification);
    }

    if (response?.success === true) {
      this.utilsService.dialog('SUCCESS', 'Exitoso', response.message?.description);
      this.goToPage('admin/notificaciones');
    } else {
      this.utilsService.dialog('ERROR', 'Error', response?.message?.description ?? 'Por favor vuelve a intentar más tarde.');
    }
  }

  async sendNotification2(isDraft: boolean) {
    // if (!this.validateSelections()) {
    //   return;
    // }

    try {
      console.log(this.statusSelected);

      if (this.statusSelected != 'Instantáneo') {
        this.convertToLocalTimezoneAndFormatToUTC(
          this.formatDate(this.dateStart),
          this.sendHour?.toString()!
        );
      }

      const notification: NotificationItem = {
        id: this.notificationId,
        eventTypeId: this.eventSelected.type,
        eventId: this.eventSelected.id,
        notificationType: isDraft
          ? 'draft'
          : this.statusSelected === 'Programado'
            ? 'scheduled'
            : 'instant',
        dateSend: this.statusSelected === 'Programado' ? this.utcSendDate! : '',
        hourSend: this.utcSendHour ?? '',
        updatedAt: '',
        createdAt: '',
        subject: this.subject,
        message: this.getPlainTextFromHtmlBody(this.message),
      };

      let response;

      if (this.statusSelected === 'Programado' || isDraft) {
        response =
          this.notificationModel == null
            ? await this.notificationsService.createScheduleNotificacion(
              notification
            )
            : await this.notificationsService.updateNotification(notification);
      } else {
        response = await this.notificationsService.createInstantNotificacion(
          notification
        );

        if (response?.success === true && this.notificationModel != null) {
          await this.deleteNotification();
        }
      }

      if (response?.success === true) {
        this.utilsService.dialog('SUCCESS', 'Exitoso', response.message?.description);
        this.goToPage('admin/notificaciones');
      } else {
        this.utilsService.dialog('ERROR', 'Error', response?.message?.description ?? 'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
    }
  }

  async deleteNotification() {
    try {
      await this.notificationsService.deleteNotification(
        this.notificationModel?.id as number
      );
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
    }
  }

  // validateSelections() {
  //   return ( this.notification.notificationType.length > 0 && this.notification.notificationType != 'seleccione' );    
  // }

  formatDate(date: NgbDateStruct) {
    return `${date.year}-${date.month.toString().padStart(2, '0')}-${date.day
      .toString()
      .padStart(2, '0')}`;
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }

  convertToLocalTimezoneAndFormatToUTC(dateStr: string, timeStr: string) {
    const localDate = new Date(`${dateStr}T${timeStr}`);
    const localOffset = localDate.getTimezoneOffset() * 60 * 1000;
    const utcDate = new Date(localDate.getTime() + localOffset);
    this.utcSendDate = utcDate.toISOString().slice(0, 10);
    this.utcSendHour = utcDate.toISOString().slice(11, 19);
  }

  getPlainTextFromHtmlBody(htmlMessage: string): string {
    const $ = cheerio.load(htmlMessage);
    return $.text();
  }

  validate() {
    // RESOLVER ERROR
    // console.log(this.sendHour, typeof this.sendHour)
    // console.log(Object.values(this.sendHour).length === 0)
    // return (this.notification.subject.length < 5 || this.notification.message.length < 17) ||
    //   (
    //     this.notification.notificationType == 'scheduled' &&
    //      (Object.entries(this.dateStart).length === 0 || Object.entries(this.dateStart).length === 0)
    //   )
    return (this.notification.subject.length < 5 || this.notification.message.length < 17) ||
           ( this.notification.notificationType == 'scheduled' && (Object.entries(this.dateStart).length === 0) )
  }
}
