import { Component, OnInit, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BackService } from '../models/back-service.model';
import { BackofficeService } from '../services/backoffice.service';
import { ErrorDialogComponent } from 'src/app/Extra/error-dialog/error-dialog.component';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

@Injectable()
export class LoginComponent implements OnInit {
  public loading: boolean = false;
  email: string = '';
  password: string = '';
  show: boolean = false;
  loginData: BackService = {
    username: '',
    password: ''
  };
  username: any;
  buttonDisabled: boolean = false;

  constructor(private router: Router, private backofficeService: BackofficeService, private modalService: NgbModal) { }

  ngOnInit() {
    this.buttonDisabled = false;
  }

  async login() {
    this.loading = true;
    this.buttonDisabled = true;

    this.loginData = {
      username: this.email,
      password: this.password
    }

    await this.backofficeService.login(this.loginData).then(
      async (response) => {
        sessionStorage.setItem('EventID', '0');
        sessionStorage.setItem('EventType', '0');
        sessionStorage.setItem('UserID', response.result.id);
        sessionStorage.setItem('Role', response.result.roles);
        sessionStorage.setItem('Email', response.result.email);
        sessionStorage.setItem('Token', response.result.accessToken);
        sessionStorage.setItem('companyId', response.result.companyId);
        localStorage.setItem('session', (new Date().getTime() + 86400000).toString()); //1min. = 60000

        await this.loadData();
      },
      (error) => {
        console.log(error);
        const modalRef = this.modalService.open(ErrorDialogComponent, { centered: true })
        modalRef.componentInstance.errorMessage = "Usuario o contraseña incorrectos.";
      }).finally(() => {
        this.loading = false
        this.buttonDisabled = false;
      })
  }

  loadData() {
    this.backofficeService.getCurrentUser(sessionStorage.getItem('Token'))
      .subscribe({
        next: (res) => {
          console.log(res.result);
          this.username = res.result.firstName + " " + res.result.firstLastname;
          sessionStorage.setItem('Name', this.username);
          this.goToPage('/admin/inicio');
        },
        error: (e) => {
          const modalRef = this.modalService.open(ErrorDialogComponent, { centered: true })
          modalRef.componentInstance.errorMessage = "Error!! Por favor vuelve a intentar más tarde.";
        }
      })
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }

  passwordVisibility() {
    this.show = !this.show;
  }
}
