import { KeyValue } from '@angular/common';
import { environment } from 'src/environments/environment';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { Component, OnInit, Injectable, ViewChild } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { CompanyService } from 'src/app/services/company.service';
import { BackServiceCompanies, listas } from 'src/app/models/back-service.model';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-edit-empresa',
  templateUrl: './edit-empresa.component.html',
  styleUrls: ['./edit-empresa.component.css']
})

@Injectable()
export class EditEmpresaComponent implements OnInit {
  @ViewChild('form') form!: NgForm;

  public comite: {}[] = [];
  public comison: {}[] = [];
  public sectors: {}[] = [];
  public loading: boolean = false;
  public sectorsList: listas[] = [];
  public commissonsList: listas[] = [];
  public committeesList: listas[] = [];
  public companyId: number | boolean = false;
  public env: boolean = environment.production;
  // public env: boolean = false;
  // public env_aux: boolean = environment.production;

  public dropdownSettings: IDropdownSettings = {
    idField: 'id',
    textField: 'name',
    itemsShowLimit: 1,
    enableCheckAll: false,
    singleSelection: false,
    allowSearchFilter: false,
    closeDropDownOnSelection: true
  };

  fileStr: string = '';

  accessToken: any;
  isShowDiv: boolean = true;
  companyData = new BackServiceCompanies();
  loaded: boolean = false;
  imageLoaded: boolean = false;
  imageSrc: string = '';

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  list: {}[] = [
    { "Nombre del usuario": "Usuario A", "Código": "EMP-12345", "Puesto": "Auditor", "Teléfono": "23567856", "Fecha de creación": "13/10/2022" },
    { "Nombre del usuario": "Usuario B", "Código": "EMP-13235", "Puesto": "Developer", "Teléfono": "23435345", "Fecha de creación": "16/11/2022" },
    { "Nombre del usuario": "Usuario C", "Código": "EMP-23456", "Puesto": "Gerente", "Teléfono": "4353543", "Fecha de creación": "5/12/2022" },
    { "Nombre del usuario": "Usuario D", "Código": "EMP-32424", "Puesto": "Auditor", "Teléfono": "4534533", "Fecha de creación": "23/10/2022" },
  ];

  categoriesHeading: string[] = [];

  constructor(
    private utilsService: UtilsService,
    private companyService: CompanyService
  ) {
    const role = sessionStorage.getItem('Role');
    (role === 'ROLE_SUPERADMIN') ? this.env = false : this.env = true;
    this.categoriesHeading = Object.keys(this.list[0]);
  }

  ngOnInit() {

    this.companyId = this.utilsService.getParam('ID') || false;
    this.companyData.status = 1;
    this.loadLists();

    if (sessionStorage.getItem('Token') && this.companyId != false) {
      this.accessToken = sessionStorage.getItem('Token');
      this.loadDatabyID();
    }
    else if (sessionStorage.getItem('Token')) {
      this.accessToken = sessionStorage.getItem('Token');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'Inicia sesión con un usuario válido.');
      this.goToPage('/login');
    }
  }

  validation_msg(name: string): boolean {
    if (this.form && this.form.controls[name]) {
      if (
        (this.form.controls[name].errors || this.form.controls[name].invalid)
        &&
        (this.form.controls[name].dirty || this.form.controls[name].touched)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  changeAssociatedState(event: any) {
    this.companyData.socio = event.target.value === 'true';
  }

  loadDatabyID() {
    this.loading = true;
    this.companyService.getCompaniesbyID(this.companyId).then(
      res => {
        this.companyData = res.result;
        this.companyData.image = { extension: '', data: '', url: res.result.image };

        if (res.result.listComites.length > 0) {
          for (const e of res.result.listComites) {

            if (e.sector != null) {
              this.sectors = [...this.sectors];
              this.sectors.push({
                id: e.sector.id,
                name: e.sector.name,
                idCrm: e.sector.idCrm
              });
            }

            if (e.comite != null) {
              this.comite = [...this.comite];
              this.comite.push({
                id: e.comite.id,
                name: e.comite.name,
                idCrm: e.comite.idCrm
              });
            }

            if (e.comision != null) {
              this.comison = [...this.comison];
              this.comison.push({
                id: e.comision.id,
                name: e.comision.name,
                idCrm: e.comision.idCrm
              });
            }
          }
        }
      },
      e => {
        this.utilsService.dialog('ERROR', 'Error', 'Intentalo de nuevo mas tarde.');
      },
    ).finally(() => {
      this.loading = false;
    });
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + index);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + index) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  onFileSelected(event: any) {
    if (event.target.files && event.target.files.length > 0) {
      this.fileStr = event.target.files[0].name;
    }

    var file = event.dataTransfer ? event.dataTransfer.files[0] : event.target.files[0];
    console.log(this.companyData.image);
    this.companyData.image!.extension = file.type.substring(file.type.indexOf('/') + 1);

    var reader = new FileReader();
    this.loaded = false;

    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e: any) {
    console.log("_handleReaderLoaded")
    var reader = e.target;
    this.imageSrc = reader.result;
    this.loaded = true;
    this.companyData.image!.data = this.imageSrc;
  }

  handleImageLoad() {
    this.imageLoaded = true;
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  submitEvent() {
    if (this.form.valid) {
      this.companyId != false ? this.editCompany() : this.createCompany();
      // if (this.companyId != false) {
      //   this.editCompany();
      // }
      // else {
      //   this.createCompany();
      // }
    } else {
      this.form.control.markAllAsTouched();
      this.utilsService.dialog('ERROR', 'Error', 'Por favor llena los campos obligatorios.');
    }
  }

  createCompany() {
    this.loading = true;
    return this.companyService.createCompany(this.companyData).then(
      async res => {
        // console.log(res);
        this.utilsService.dialog('SUCCESS', 'Exito', res.message.description, '/admin/users/admin-empresas');
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
        throw new Error(error);
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  editCompany() {
    this.loading = true;
    return this.companyService.updateComapny(this.companyId, this.companyData).then(
      async res => {
        // console.log(res);
        this.utilsService.dialog('SUCCESS', 'Exito', res.message.description, '/admin/users/admin-empresas');
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
        throw new Error(error);
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  loadLists() {
    this.utilsService.getCommissions().then(
      (response) => {
        this.commissonsList = response.result.comisiones;
      },
      (error) => {
        console.log(error)
      });

    this.utilsService.getCommittees().then(
      (response) => {
        this.committeesList = response.result.comites;
      },
      (error) => {
        console.log(error)
      });

    this.utilsService.getSectors().then(
      (response) => {
        this.sectorsList = response.result.sectors;
      },
      (error) => {
        console.log(error)
      });
  }

  goToPageParams(pageName: string, itemParams: string) {
    this.utilsService.goToPage(pageName, { type: itemParams });
  }
}
